<template>
  <div id="app">
    <!-- 遮罩 -->
    <div
      class="mack"
      v-show="mack"
      @click="(mack = false), (optionsShow = false), (production = false)"
    ></div>
    <!-- 密码界面 -->
    <div class="open" v-show="open.showP">
      <img src="./assets/huama.png" alt="" />
      <div class="titlePassword">提示：请向管理员索要通行码</div>
      <!-- 密码输入框 -->
      <van-password-input
        class="password"
        :value="value"
        :focused="showKeyboard"
        @focus="showKeyboard = true"
      />
      <!-- 带右侧栏的键盘 -->
      <van-number-keyboard
        class="keyboard"
        v-model="value"
        :show="showKeyboard"
        theme="custom"
        extra-key="."
        close-button-text="完成"
        @blur="showKeyboard = false"
        @input="onInput"
      />
    </div>
    <div class="content">
      <!-- 头部 -->
      <div class="top">
        <p>
          <img src="./assets/huama.png" alt="" />
          <!-- 图片上传 -->
          <input
            type="file"
            class="photoFime"
            name="upload_file"
            id="uploadFile"
            ref="foreheadCir"
            mutiple="mutiple"
            accept="image/*"
            @change="uploadImg()"
          />
        </p>
        <p><span @click="confirm($event)">专业的人用专业的软件</span></p>
        <p><span @click="addMachine($event)">癞蛤蟆你兄弟超专业</span></p>
        <!-- 右上角点击 -->
        <div class="showOptions" @click="(mack = true), (optionsShow = true)">
          <span v-show="nowTime"></span>
        </div>
        <!-- 显示日期 --- 显示记录时间 -->
        <div class="timeShow" @click="timeShow($event)"></div>
        <div class="options" v-show="optionsShow">
          <div class="reset" @click="showReset = !showReset">
            重置机台<i class="iconfont icon-zhiwu"></i>
          </div>
          <div class="clearAllf" @click="clearAllf($event)">
            删除非收藏<i class="iconfont icon-yongyan"></i>
          </div>
          <div
            class="other"
            @click="(production = !production), (optionsShow = false)"
          >
            其他<i class="iconfont icon-remai"></i>
          </div>
          <div class="back" @click="back">
            退出
            <i class="iconfont icon-a-Group427318885"></i>
          </div>
        </div>
        <!-- 清除所有机台数据弹窗 -->
        <div class="confirm" v-show="showReset">
          <p>确定清除所有机<br />台数据吗?</p>
          <div class="left" @click="clear($event)">取消</div>
          <div class="right" @click="determine($event)">确定</div>
        </div>
      </div>
      <!-- 全部机台 -->
      <div class="por">
        <div
          v-for="(item, index) in indexData.length"
          :key="index"
          class="machine"
          :id="index"
        >
          <input
            type="text"
            placeholder="0 #"
            :class="ipt_one ? 'clearIpt iptOne' : 'clearIpt'"
            @input="ipt($event)"
            :value="indexData[index].code"
          />
          <input
            type="text"
            placeholder="0000"
            class="clearIpt"
            @input="ipt($event)"
            :value="indexData[index].coding"
          />
          <input
            type="text"
            placeholder="产品"
            class="clearIpt"
            @input="ipt($event)"
            :value="indexData[index].product"
          />
          <input
            type="text"
            placeholder="9999"
            class="clearIpt"
            @input="ipt($event)"
            :value="indexData[index].target"
          />
          <!-- 计算 -->
          <input
            class="countIpt clearIpt"
            type="text"
            @input="ipt($event)"
            :value="indexData[index].count"
          />
          <!-- 结果 -->
          <input
            type="button"
            class="countResult"
            :value="indexData[index].result"
            disabled
          />
          <!-- 清除 -->
          <input
            type="button"
            @click="clearIpts($event)"
            :style="{
              background: indexData[index].clear,
              color: indexData[index].color,
            }"
            class="clearBtn"
            value="清除"
          />
          <!-- 输入数据显示时间 -->
          <ul class="time">
            <li
              v-for="(item, i) in 7"
              :key="i"
              v-html="indexData[index].liData[i]"
              v-show="nowTime"
            ></li>
          </ul>
          <div class="slide" @click="slide($event)"></div>
          <!-- 清除弹窗提示 -->
          <div class="confirm">
            <p>确定清除这个机 <br />台数据吗?</p>
            <div class="left" @click="clear($event)">取消</div>
            <div class="right" @click="cancel($event)">确定</div>
          </div>
          <div class="rightBtn">
            <div class="collection" @click="clickCollection($event)">收藏</div>
            <div class="delete" @click="clickDelete($event)">删除</div>
          </div>
        </div>
      </div>
      <!-- 其他选项中的排产 -->
      <div class="production" v-show="production">
        <p>{{ dataTime() }}</p>
        <p v-for="(item, index) in ccc[0]" :key="index" :id="index">
          {{ ccc[index] }}
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Vue from "vue";
import Vant from "vant";
import "vant/lib/index.css";

Vue.use(Vant);
export default {
  name: "App",
  data() {
    return {
      // 存储的数据
      indexData: [],
      // 记录的时间显示/隐藏 ul
      nowTime: true,
      // 日期显示隐藏
      time: false,
      // 显示重置弹窗
      showReset: false,
      // 右上角选项弹窗
      optionsShow: false,
      // 遮罩
      mack: false,
      // 排产
      production: false,
      // 排产
      ccc: "",
      // 点击收藏后为true
      collection: false,
      // 触发滑动事件true
      move: false,
      // vant 组件数据 ------------
      value: "",
      showKeyboard: true,
      // 密码通行证
      open: {
        showP: false,
        t: "",
      },
      // 机台滑动时机台号#跟着滑动
      ipt_one: false,
      // 点击右滑
      slideFlag: true,
    };
  },
  methods: {
    // 退出
    back() {
      this.mack = false;
      this.optionsShow = false;
      this.production = false;
      location.reload();
    },
    // 日期显示
    timeShow() {
      this.nowTime = !this.nowTime;
      let youShangJiao = document.querySelector(".timeShow");
      let time = new Date();
      let month = time.getMonth() + 1;
      let date = time.getDate();
      month < 10 ? (month = "0" + month) : month;
      let now_time = month + "月" + date + "日";
      if (this.nowTime) {
        youShangJiao.innerText = now_time;
        youShangJiao.style.color = "#fff";
      } else {
        youShangJiao.innerText = "";
      }
    },
    // 调用清除弹窗
    clearIpts(e) {
      this.mack = true;
      // 清除按钮的背景颜色
      let bgColor = e.currentTarget.style.background;
      // 点击当前的清除弹窗
      let confirm = e.currentTarget.parentElement.querySelector(".confirm");
      if (bgColor == "green") {
        confirm.style.display = "block";
      }
    },
    // 点击确定按钮清除机台数据（不删除机台）
    cancel(e) {
      // 确定清除弹窗元素
      let confirm = e.currentTarget.parentNode;
      // 确定清除弹窗元素父级元素
      let fatherTarget = confirm.parentNode;
      // 所有input框元素
      let ipts = fatherTarget.querySelectorAll("input");
      let iptLen = ipts.length;
      //  清除按钮
      let clearBtn = fatherTarget.children[6];
      // 计算结果元素
      let countResult = fatherTarget.children[6];
      // 所有时间元素
      let times = fatherTarget.querySelectorAll("li");
      let liLen = times.length;
      for (let i = 0; i < iptLen - 2; i++) {
        ipts[i].value = "";
      }
      for (let i = 0; i < liLen; i++) {
        times[i].innerHTML = "";
      }
      clearBtn.style.background = "";
      countResult.value = "= 0";
      confirm.style.display = "none";
      this.indexData[fatherTarget.id] = {
        code: "",
        coding: "",
        product: "",
        target: "",
        clear: "",
        count: "",
        result: "= 0",
        liData: "",
      };
      localStorage.setItem("indexData", JSON.stringify(this.indexData));
    },
    // 点击确定按钮清除(所有机台数据)
    determine() {
      this.mack = false;
      this.optionsShow = false;
      this.showReset = false;
      this.production = false;
      // 所有机台
      let machine = document.querySelectorAll(".machine");
      let machineLen = machine.length;
      for (let i = 0; i < machineLen; i++) {
        let input = machine[i].children;
        let inputLen = input.length;
        // children前五个才是有value的input
        for (let j = 0; j < inputLen - 4; j++) {
          input[j].value = "";
        }
      }
      this.indexData = new Array(4).fill({
        code: "",
        coding: "",
        product: "",
        target: "",
        clear: "",
        count: "",
        result: "= 0",
        liData: "",
      });
      localStorage.setItem("indexData", JSON.stringify(this.indexData));
    },
    // 点击取消按钮
    clear(e) {
      this.mack = false;
      this.optionsShow = false;
      this.showReset = false;
      this.production = false;
      // 确定清除弹窗元素
      let confirm = e.currentTarget.parentElement;
      confirm.style.display = "none";
    },
    // 点击logo添加机台
    addMachine(e) {
      // 当前元素的父级的所有input元素
      let ancestorTarget = e.currentTarget.parentNode.parentNode.parentNode;
      let machine = ancestorTarget.querySelectorAll(".machine");
      let machineLen = machine.length;
      let add = {
        code: "",
        coding: "",
        product: "",
        target: "",
        clear: "",
        count: "",
        result: "= 0",
        liData: "",
      };
      this.indexData[machineLen] = add;
      this.indexData.machineLen++;
      localStorage.setItem("indexData", JSON.stringify(this.indexData));
    },
    // 点击专业的人用专业的软件 (删除机台)
    confirm(e) {
      // 当前元素的父级的所有input元素
      let ancestorTarget = e.currentTarget.parentNode.parentNode.parentNode;
      let machine = ancestorTarget.querySelectorAll(".machine");
      let machineLen = machine.length;
      let iptVal = machine[machineLen - 1].querySelectorAll("input");
      let falg = true;
      for (let i = 0; i < iptVal.length - 2; i++) {
        if (iptVal[i].value != "") {
          alert("存在数据不能删除机台");
          falg = false;
          break;
        }
      }
      if (falg) {
        this.indexData.machineLen--;
        localStorage.setItem("indexData", JSON.stringify(this.indexData));
      }
    },
    // 点击右上角空白，清除(所有机台数据)
    reset(e) {
      this.showReset = true;
    },
    // input输入时调用
    ipt(e) {
      let that = this;
      // 当前目标元素
      e = e.currentTarget;
      // 目标父级的元素
      let fatherTarget = e.parentNode;
      // 清除按钮元素
      let clearBtn = fatherTarget.children[6];
      // 目标父级元素的所有input元素
      let ipt = fatherTarget.querySelectorAll("input");
      let iptLen = ipt.length;
      for (let i = 0; i < iptLen - 2; i++) {
        if (ipt[i].value != "") {
          // 只要有一个不是空，就深绿色
          clearBtn.style.background = "green";
          break;
        } else {
          // 所有的input都是空的
          clearBtn.style.background = "";
        }
      }
      // 计算input框和计算结果显示调用
      that.countOrResult(fatherTarget);
    },
    // --------------------
    // --------------------
    // --------------------
    // 计算input框和计算结果显示
    countOrResult(e) {
      let that = this;
      // 计算结果元素
      let countResult = e.children[5];
      // 当前目标父元素所有li
      let li = e.querySelectorAll("li");
      let liLen = li.length;
      // 当前目标元素的计算input框（清楚元素在[5]）
      let inputCount = e.children[4];
      // --------------------
      // 当前目标元素的计算input框value
      let valCount = inputCount.value;
      // --------------------
      // 失去焦点之前的value以 + 号分割转成数组 用于计数数据
      let valCountSplit = valCount.split("+");
      let lenCount = valCountSplit.length;
      let Xflag = false;
      let valCountSplitX;
      let lenCountX;
      // 这里×是手机搜狗乘法号，x是字母x
      if (valCount.includes("×") || valCount.includes("x")) {
        Xflag = true;
        valCountSplitX = valCount.includes("×")
          ? valCount.split("×")
          : valCount.split("x");
        valCountSplit = valCountSplitX[0].split("+");
        lenCountX = valCountSplitX.length;
      }
      // --------------------
      let sum;
      // --------------------------------
      // 当前目标元素失去焦点触发
      for (let i = 0; i < 5; i++) {
        e.children[i].onblur = function () {
          sum = 0;
          if (inputCount.value == "") {
            for (let i = 0; i < liLen; i++) {
              li[i].innerHTML = "";
            }
            countResult.value = "= 0";
            that.setStorage(e);
            return;
          }
          for (let i = 0; i < lenCount; i++) {
            sum += Number(valCountSplit[i]);
          }
          if (Xflag) {
            sum = Number(sum * valCountSplitX[1]);
          }
          countResult.value = "= " + sum;
          // 当前目标元素的value以 "" 号分割转成数组 用于判断最后一个数据是不是 + 号
          let valIf = inputCount.value.split("");
          let lenIf = valIf.length;
          // 失去焦点后，如果最后一组数据不是 + 号
          if (valIf[lenIf - 1] != "+") {
            inputCount.value = inputCount.value + "+";
          }
          // 失去焦点后，第一个input + ” #“
          let jjj = e.children[0].value.split("");
          let jjjLen = jjj.length;
          if (jjj[jjjLen - 1] != "#") {
            e.children[0].value = e.children[0].value + "#";
          }
          // li的第原数数据以+分割后的第最后一位
          li[lenCount - 1].innerHTML = that.getNowTime();
          // 调用本地储存函数 储存父级所有元素
          that.setStorage(e, lenCount);
        };
      }
    },
    // 获取时间 时分秒
    getNowTime() {
      var nowTime = "";
      var date = new Date();
      var sign2 = ":";
      var hour = date.getHours(); // 时
      var minutes = date.getMinutes(); // 分
      if (hour >= 0 && hour <= 9) {
        hour = "0" + hour;
      }
      if (minutes >= 0 && minutes <= 9) {
        minutes = "0" + minutes;
      }
      nowTime = hour + sign2 + minutes;
      return nowTime;
    },
    // 存储数据
    setStorage(e, lenCount) {
      lenCount = lenCount ? lenCount : "";
      let id = e.id;
      // 所有input 机台号 编码 产品 目标 清除 计算 结果 时间ul
      let code = e.children[0].value;
      let coding = e.children[1].value;
      let product = e.children[2].value;
      let target = e.children[3].value;
      let count = e.children[4].value;
      let clear = e.children[6].style.background;
      let result = e.children[5].value;
      let li = e.children[7].children;
      let liLen = li.length;
      let liData = new Array(liLen);
      for (let i = 0; i < lenCount; i++) {
        liData[i] = li[i].innerHTML;
      }
      // 机台数量
      let machineLen = e.parentNode.querySelectorAll(".machine").length;
      this.indexData.machineLen = machineLen;
      for (let i = 0; i < machineLen; i++) {
        if (id == i) {
          this.indexData[i] = {
            code: code,
            coding: coding,
            product: product,
            target: target,
            clear: clear,
            count: count,
            result: result,
            liData: liData,
          };
        }
      }
      localStorage.setItem("indexData", JSON.stringify(this.indexData));
    }, // 点击上传图片
    uploadImg() {
      let file = document.getElementById("uploadFile").files[0];
      let reader = new FileReader();
      reader.readAsDataURL(file);
      //   ========================
      let youShangJiao = document.querySelector(".showOptions span");
      let time = new Date();
      let month = time.getMonth() + 1;
      let date = time.getDate();
      month < 10 ? (month = "0" + month) : month;
      let now_time = month + "月" + date + "日";
      youShangJiao.innerText = "正在识别 " + now_time + " 排产  ! ! ";
      youShangJiao.style.color = "#fff";
      //   ========================
      let a = "";
      reader.onload = function (result) {
        a = result.target.result;
        a = a.split(",")[1];
        let form = new FormData();
        form.append("image", a);
        axios({
          method: "post", //请求方式，默认是get请求
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
          },
          url: "https://aip.baidubce.com/rest/2.0/ocr/v1/doc_analysis_office?access_token=24.70ecf7c38d1f91bad8c8b2ba5f0d66ce.2592000.1670062449.282335-27736960", //地址
          data: form,
        })
          .then((res) => {
            let data = res.data.results;
            console.log(data);
            let len = data.length;
            // 拿取需要的数据
            let obj = [];
            // 拿取分割后的数据
            let objSplit = [];
            let left = "";
            for (let i = 0; i < 50; i++) {
              // 收集第一个的机台位置
              if (
                (data[i].words.word == "A" || data[i].words.word == "B") &&
                !!data[i].words
              ) {
                left = data[i + 1].words.words_location.left;
                break;
              }
            }
            for (let i = 0; i < len; i++) {
              if (
                left == data[i].words.words_location.left &&
                !!data[i].words
              ) {
                obj.push("aaaaa" + data[i].words.word);
              } else {
                obj.push(data[i].words.word);
              }
            }
            objSplit = obj.join().split("aaaaa");
            objSplit.splice(0, 1);
            let objLen = objSplit.length;
            let objData = [];
            for (let i = 0; i < objLen; i++) {
              objSplit[i] = objSplit[i].split(",");
              objData.push({
                code: objSplit[i][0] + "#",
                coding: objSplit[i][2].slice(-4),
                product: objSplit[i][4].slice(0, 3),
                target: objSplit[i][10],
                clear: "green",
                count: "",
                result: "= 0",
                liData: "",
              });
            }
            localStorage.setItem("indexData", JSON.stringify(objData));
            localStorage.setItem("p", JSON.stringify(objSplit));
            location.reload();
          })
          .catch((err) => {
            console.log(err);
            youShangJiao.innerText = "识别出错！！";
          });
      };
    },
    // 点击右滑
    slide(e) {
      e = e.currentTarget.parentNode.parentNode;
      if (this.slideFlag) {
        e.style.transition = "margin-left 0.1s";
        e.style.marginLeft = "-2.6667rem";
        this.ipt_one = true;
      } else {
        e.style.transition = "margin-left 0.1s";
        e.style.marginLeft = "0.266rem";
        this.ipt_one = false;
      }
      this.slideFlag = !this.slideFlag;
    },
    // 点击收藏按钮
    clickCollection(e) {
      e = e.currentTarget;
      let target = e.parentNode.parentNode.children[6];
      let id = e.parentNode.parentNode.id;
      var str = localStorage.getItem("indexData");
      if (str) {
        str = JSON.parse(str);
        if (target.style.color != "rgb(255, 255, 255)") {
          target.style.color = "rgb(255, 255, 255)";
          str[id].color = "rgb(255, 255, 255)";
        } else {
          target.style.color = "";
          str[id].color = "";
        }
        localStorage.setItem("indexData", JSON.stringify(str));
      }
      var str2 = localStorage.getItem("indexData");
    },
    // 点击删除按钮
    clickDelete(e) {
      e = e.currentTarget;
      let target = e.parentNode.parentNode.children[6];
      if (target.style.color != "rgb(255, 255, 255)") {
        this.removeM(target);
      }
    },
    // 删除函数封装
    removeM(target) {
      let machine = target.parentNode;
      let id = machine.id;
      machine.remove();
      var str = localStorage.getItem("indexData");
      if (str) {
        str = JSON.parse(str);
        // 删除对象
        this.$delete(str, id);
        localStorage.setItem("indexData", JSON.stringify(str));

        var str2 = localStorage.getItem("p");
        if (str2) {
          str2 = JSON.parse(str2);
          let production = document.querySelectorAll(".production p");
          if (production[id]) {
            production[id].remove();
          }
          this.$delete(str2, id);
        }
        localStorage.setItem("p", JSON.stringify(str2));
        location.reload();
      }
    },
    // 删除非收藏
    clearAllf() {
      this.mack = false;
      this.optionsShow = false;
      this.showReset = false;
      this.production = false;
      //   let clearBtn = document.querySelectorAll(".clearBtn");
      //   let len = clearBtn.length;
      var str = localStorage.getItem("indexData");
      var str2 = localStorage.getItem("p");

      if (!str) return;
      if (!str2) return;
      str = JSON.parse(str);
      str2 = JSON.parse(str2);
      let len = str.length;
      let obj = [];
      let obj2 = [];
      for (let i = 0; i < len; i++) {
        if (!!str[i].color) {
          obj.push(str[i]);
          obj2.push(str2[i]);
        }
      }
      localStorage.setItem("indexData", JSON.stringify(obj));
      localStorage.setItem("p", JSON.stringify(obj2));
      location.reload();
    },
    // input输入触发
    onInput() {
      // 999.33
      if (this.value == "999.3") {
        this.open.showP = false;
        // 解锁后的时间 存储起来
        let obj = [];
        let date = new Date();
        let t = date.setDate(date.getDate() + 3);
        obj.t = new Date(t);
        obj.showP = false;
        localStorage.setItem("timePasswords", JSON.stringify(obj));
      }
    },
    // 其他排产中的时间显示
    dataTime() {
      let time = new Date();
      let year = time.getFullYear();
      let month = time.getMonth() + 1;
      let date = time.getDate();
      let hour = time.getHours();
      let minutes = time.getMinutes();
      let second = time.getSeconds();
      month < 10 ? (month = "0" + month) : month;
      // hour < 10 ? (hour = "0" + hour) : hour;
      let hourtext = hour >= 12 ? "下午" : "上午";
      hour >= 12 ? (hour = hour - 12) : hour;
      minutes < 10 ? (minutes = "0" + minutes) : minutes;
      second < 10 ? (second = "0" + second) : second;
      let now_time =
        // "当前时间：" +
        year +
        "年" +
        month +
        "月" +
        date +
        "日" +
        " " +
        // show_day[day - 1] +
        " " +
        hourtext +
        hour +
        ":" +
        minutes +
        ":" +
        second;
      return now_time;
    },
  },
  mounted() {},
  created() {
    //获取localStroage值
    var str = localStorage.getItem("indexData");
    var str2 = localStorage.getItem("p");
    var obj = [];
    var obj2 = [];
    if (str) {
      obj = JSON.parse(str);
      obj2 = JSON.parse(str2);
      this.ccc = obj2;
      if (obj.machineLen < 0 || obj.machineLen == 0) {
        // 默认生成机台对应对象健值
        obj = new Array(4).fill({
          code: "",
          coding: "",
          product: "",
          target: "",
          clear: "",
          color: "",
          count: "",
          result: "= 0",
          liData: "",
        });
      }
    } else {
      // 默认生成机台对应对象健值
      obj = new Array(4).fill({
        code: "",
        coding: "",
        product: "",
        target: "",
        clear: "",
        color: "",
        count: "",
        result: "= 0",
        liData: "",
      });
    }
    this.indexData = obj;
    // --------------------------
    // --------------------------
    // --------------------------
    var timePasswords = localStorage.getItem("timePasswords");
    let obj3 = {};
    if (timePasswords) {
      timePasswords = JSON.parse(timePasswords);
      this.open.showP = timePasswords.showP;
      // 如果现在的时间毫秒数 > 解锁后的时间毫秒数 + 3天的时间
      if (new Date().getTime() > new Date(timePasswords.t).getTime()) {
        this.open.showP = true;
        // 状态储存起来
        obj3.showP = this.open.showP;
        localStorage.setItem("timePasswords", JSON.stringify(obj3));
      }
    } else {
      // 如果现在的时间毫秒数 > 设置的时间毫秒数
      if (new Date().getTime() > new Date("2022/11/14/6:30").getTime()) {
        this.open.showP = true;
        // 状态储存起来
        obj3.showP = this.open.showP;
        localStorage.setItem("timePasswords", JSON.stringify(obj3));
      }
    }
  },
  beforeCreate() {},
};
</script>
